<template>
   <div class="minePig">
       <div class='pig_top'>
           <div class='return' @click='retreat()'></div>
           <span>我的猪猪</span>
       </div>
       <div class='pig_cen'>
           <div 
           v-for="(item,index) in arr" 
           :key='index'
           @click='skip(index)'>
               <span></span>
               <div>
                   <div class='pig_head'>
                       <img :src="item.img" alt=""/>
                   </div>
                   <div class='pig_name'>
                       <span>
                           {{item.title}}
                           <img :src="require('../../../static/images/mine_pig/level'+ (index+1) +'.png')" alt=''/>
                        </span>
                       <em>
                           <p>价格： 200.00xtoken</p>
                           <p>产能： 2.5000</p>
                       </em>
                   </div>
               </div>
           </div>
       </div>
   </div>
</template>

<script>

import axios from 'axios'

export default {
   name: 'minePig',
   data() {
       return {
           arr:[
               { title:'佩奇' , img:require('../../../static/images/mine_pig/v1pig.png')},
               { title:'伊比利亚' , img:require('../../../static/images/mine_pig/v2pig.png')},
               { title:'巴克夏猪' , img:require('../../../static/images/mine_pig/v3pig.png')},
               { title:'红山草猪' , img:require('../../../static/images/mine_pig/v4pig.png')}
           ],
           data:[]
       }
   },
   created(){
       this.getPig();
   },
   methods:{
       retreat(){
           this.$router.go(-1);
       },
       getPig(){
           let url = '/farm/getallpigs';
           this.$http.post(url,{
               cmd:'',
               body:{
                   accessToken:localStorage.token
               }
           }).then( (res) => {
               this.data = res;
            //    console.log(this.data);
           })
       },
       skip(index){
           let Data = [];
           this.data.forEach( (item) =>{
               if( item.goodsid === (index+1) ){
                   Data.push(item);
               }
           })
           this.$router.push({path:'/pig',query:{data:Data}})
       }
   },
}
</script>

